#header {
  display: grid;
  grid-gap: 20px;
  padding: 1rem;
  -ms-flex-align: center;
      align-items: center;
}

h2 {
  display: grid;
  margin-left: 3vw;
  grid-template-columns: 1fr auto 1fr;
  grid-gap: 20px;
  -ms-flex-align: center;
      align-items: center;
}

h2:before {
  display: block;
  content: '';
  height: 10px;
  background: -webkit-gradient(linear, right top, left top, from(#ffc600), to(transparent));
  background: -webkit-linear-gradient(right, #ffc600, transparent);
  background: -o-linear-gradient(right, #ffc600, transparent);
  background: linear-gradient(to left, #ffc600, transparent);
}
h2:after {
  display: block;
  content: '';
  height: 10px;
  background: -webkit-gradient(linear, left top, right top, from(#ffc600), to(transparent));
  background: -webkit-linear-gradient(left, #ffc600, transparent);
  background: -o-linear-gradient(left, #ffc600, transparent);
  background: linear-gradient(to right, #ffc600, transparent);
}

h1,
h2 {
  font-family: sans-serif;
  text-align: center;
}



